import { useState } from 'react'
import { FaCreativeCommonsPdAlt } from 'react-icons/fa';
import './App.css';
import Header from './Components/Header';
import Task from './Components/Task';
import Tasks from './Components/Tasks';
import AddTask from './Components/AddTask';
function App() {

  const [showAddTask,setShowAddTask] = useState(false);
  const [tasks, setTasks] = useState([

    {
       id: 1,
       text: "Doctors Appointment",
       day:"Sep 21th at 2:30pm",
       reminder:true,

     },
     {

        id: 2,
        text: "Meeting a School",
        day:"Sep 21th at 4:30pm",
        reminder:true,
 
      },
     
      {

        id: 3,
        text: "Food Shopping",
        day:"Sep 22th at 8:30am",
        reminder:false,
 
      },


  ])

 
    const addTask = (task) =>{

      const id=Math.floor(Math.random() * 1000) + 1
      //not inevitable we can avoid this


      const newTask = {id,...task}  //we get the text id and remainder passed from addtask here

      //id is not required
    
     setTasks([...tasks,newTask]); //Adding we got task to the entire list :: ...tahas
    

    }



    const deleteTask = (id) =>{

        //  setTasks(tasks.filter((task) => task.id !== id))
       setTasks(tasks.filter((task) => task.id !== id ))
  

    }
 
    const toggleReminder = (id) =>{

      
      setTasks(tasks.map((task) => task.id === id ? {...task, reminder: !task.reminder } : task  ))
 
        
        ///To access the remainder property of the object task we use ...tasks sread operator which helps to access the entire properties of 
          //tasks current task is equal to the id we passsed it will be checkeed that particular id 's remaineder is set to false if it is true
    //to access the remainder property of the object task we use ...tasls spread operator which helps to access the entire
    }

      /* { showAddTask ? <AddTask onAdd={addTask} /> : "" } */

      // when user click on the Header component header component when press
      //in a button onlick calls onAdd then onAdd perform setShowAddTask to True when it is true then 
      //it shows AddTask component  and we pass the addTask function to that and we get 

  return (
    <div className="container">


  
      <Header title="Task Tracker" onAdd={() => setShowAddTask(!showAddTask)} showAdd={showAddTask} />



      { showAddTask && <AddTask onAdd = {addTask} /> }


      

    {

      tasks.length > 0 ? ( <Tasks tasks={tasks} onDelete={deleteTask} onToggle={toggleReminder} /> ) 
      
      : ("No task to show")


    }

    </div>
  );
}

export default App;
