import React from 'react'
import Button from './Button'



function Header({title,onAdd,showAdd}) {


   

    return (
        
         <header className="header">
    
            <h1>{title}</h1>
     
           <Button color={showAdd ? ' rgb(255 35 120) ' : ' rgb(255 122 8) '} text= {showAdd ? 'Close' : "Add"} onClick={onAdd} />


         </header>
         

    )
}

Header.defaultProps ={

 title:"Hai Welcome to react"

}

// const colorbtn={

